import { Component, Input } from "@angular/core";

@Component({
    selector: 'app-image-panel',
    templateUrl: './img-panel.component.html',
    styleUrls: ['./img-panel.component.scss']
})
export class AppImagePanelComponent {
    _binaryData: any;
    _width: number;
    _height: number;
    _path = '';

    @Input('path')
    public set path(path: string) {
        this._path = path;
    }

    public get path() {
        return this._path;
    }

    @Input('width')
    public set width(width: number) {
        this._width = width;
    }

    public get width() {
        return this._width;
    }

    @Input('height')
    public set height(height: number) {
        this._height = height;
    }

    public get height() {
        return this._height;
    }

    @Input('binaryData')
    public set binaryData(binaryData: any) {
        this._binaryData = binaryData ? `data:image/png;base64,${binaryData}` : null;
    }

    public get binaryData() {
        return this._binaryData;
    }
}
