import { Component, Output, EventEmitter } from "@angular/core";
import { AppModalNewsContnetComponent } from './modal-news-content.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { News } from 'src/app/main/pages/news/model/news.model';

@Component({
    selector: 'app-modal-news',
    templateUrl: './modal-news.component.html',
    styleUrls: ['./modal-news.component.scss']
})
export class AppModalNewsComponent {

    @Output()
    notifyCollectedData: EventEmitter<FormData>;

    constructor(public modalService: NgbModal) {
        this.notifyCollectedData = new EventEmitter<FormData>();
    }

    open = () => {
        const modalRef = this.modalService.open(AppModalNewsContnetComponent);
        modalRef.componentInstance.notifyCollectedItem.subscribe(formData => {
            this.notifyCollectedData.emit(formData);
        });
    }
}
