import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Gallery } from './model/galery.model';
import { environment } from 'src/environments/environment';
import { NgxGalleryImage } from 'ngx-gallery';
import { map } from 'rxjs/operators';

@Injectable()
export class GalleryService {

    constructor(private httpClient: HttpClient) { }

    persistImage = (url: string, body: FormData): Observable<Object> => {
        return this.httpClient.post(url, body);
    }

    getGallery = (url: string): Observable<NgxGalleryImage[]> => {
        return this.httpClient
            .get<Gallery[]>(url)
            .pipe(
                map(gallery => this.provideGalleryData(gallery))
            );
    }

    removeImage = (id: number | string): Observable<Object> => {
        const params = new HttpParams().set('id', `${id}`);
        const url = `${environment.serverUrl}/api/deleteimage`;
        return this.httpClient.delete(url, { params: params });
    }

    provideGalleryData = (gallery: Gallery[]): NgxGalleryImage[] => {

        const ngxGalleryImage: NgxGalleryImage[] = gallery.map(image => new NgxGalleryImage(
            {
                small: `data:image/png;base64,${image.images}`,
                medium: `data:image/png;base64,${image.images}`,
                big: `data:image/png;base64,${image.images}`,
                label: `${image.id}`
            }));

        return ngxGalleryImage;

    }


}
