import { Injectable } from '@angular/core';
import { LoginModel } from '../shared/login-dialog/login.model';
import { HttpHeaders, HttpClient, HttpResponse } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { PermissionManagerService } from './permission-manager.service';

@Injectable({
    providedIn: 'root'
})
export class LoginService {
    constructor(private httpClient: HttpClient, private permissionManagerService: PermissionManagerService) { }

    runAuthentication = (loginModel: LoginModel): Observable<HttpResponse<Object>> => {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json'
            })
        };
        const url = `${environment.serverUrl}/api/auth/login`;
        return this.httpClient.post(url, loginModel, { headers: httpOptions.headers, observe: 'response' });

    }

    logOff = (): void => {
        localStorage.clear();
        this.permissionManagerService.takeOffPermission();
    }
}

