import { Component, EventEmitter, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AppModalRunnerContentComponent } from './modal-runner-content.component';
import { Runner } from '../../main/pages/runners/model/runner.model';

@Component({
    selector: 'app-modal-runner',
    templateUrl: './modal-runner.component.html',
    styleUrls: ['./modal-runner.component.scss']
})
export class AppModalRunnerComponent {

    @Output()
    notifyCollectedData: EventEmitter<Runner>;

    constructor(public modalService: NgbModal) {
        this.notifyCollectedData = new EventEmitter<Runner>();
    }

    open = () => {
        const modalRef = this.modalService.open(AppModalRunnerContentComponent, { windowClass: 'my-class' });
        modalRef.componentInstance.notifyCollectedItem.subscribe(runner => {
            this.notifyCollectedData.emit(runner);
        });
    }

}
