import { Component, OnInit } from "@angular/core";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { LoginModel } from '../login-dialog/login.model';


@Component({
    selector: 'app-test',
    templateUrl: './test.component.html',
    styleUrls: ['./test.component.scss']
})
export class TestComponent implements OnInit {

    showPage: any;
    imgWidth = 400;
    imgHeigh = 500;

    urlToFileUpload = `${environment.serverUrl}/api/file`;

    loginM: LoginModel = {
        username: 'mbielacha',
        password: 'emc2enginner'
    };

    constructor(private http: HttpClient) {
    }

    ngOnInit(): void {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json'
            })
        };
        const url = `${environment.serverUrl}/api/auth/login`;
        this.http.post(url, this.loginM, { headers: httpOptions.headers, observe: "response" })
            .subscribe(res => {
                localStorage.setItem('token', res.headers.get('Authorization'));
            });
        this.testNews();
    }

    runTestRequest = () => {
        this.http.get<String>(environment.serverUrl + '/api', {
            responseType: 'text' as 'json'
        }).subscribe(value => {
            console.log('Api returned: ' + value);
        });
    }

    testNews = () => {
        this.http.get<String>(environment.serverUrl + '/api/news').subscribe(value => {
            console.log('Api returned: ' + value);
            this.showPage = 'data:image/png;base64,' + (<any>value).teammatePicture;
        });
    }

}
