import { Injectable, EventEmitter } from '@angular/core';

@Injectable()
export class GridExchangeDataService {
    private _dataSource: EventEmitter<string>;

    constructor() {
        this._dataSource = new EventEmitter<string>();
    }

    get dataSource(): EventEmitter<string> {
        return this._dataSource;
    }

    notifyDataChange = (data: string) => {
        this.dataSource.emit(data);
    }
}
