import { Component, Output, EventEmitter } from "@angular/core";
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Runner } from '../../main/pages/runners/model/runner.model';

@Component({
    selector: 'app-modal-runner-content',
    templateUrl: './modal-runner-content.component.html',
    styleUrls: ['./modal-runner-content.component.scss']
})
export class AppModalRunnerContentComponent {

    @Output() notifyCollectedItem: EventEmitter<any>;
    runner: Runner = {
        name: null,
        category: null,
        location: null,
        bio: null,
        five: null,
        ten: null,
        half: null,
        marathon: null
    };

    constructor(public activeModal: NgbActiveModal) {
        this.notifyCollectedItem = new EventEmitter<any>();
    }

    takeMyBreth() {
        this.notifyCollectedItem.emit(this.runner);
        this.activeModal.close();
    }
}
