import { Injectable } from '@angular/core';
import { ServerDataService } from 'src/app/core/server-data.service';
import { RequestDataModel } from 'src/app/core/request-data.model';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Runner } from './model/runner.model';
import { environment } from 'src/environments/environment';

@Injectable()
export class RunnerService {

    constructor(private serviceDataService: ServerDataService<Runner>,
        private httpClient: HttpClient) { }

    persistRunner = (url: string, runner: Runner): Observable<Object> => {
        const requestDataModel: RequestDataModel = new RequestDataModel();
        requestDataModel.url = url;
        requestDataModel.body = runner;
        return this.serviceDataService.postData(requestDataModel);
    }

    getRunners = (url: string): Observable<Runner[]> => {
        const requestDataModel: RequestDataModel = new RequestDataModel();
        requestDataModel.url = url;
        return this.httpClient.get<Runner[]>(requestDataModel.url);
    }

    uploadRunnerPhoto = (url: string, body: FormData): Observable<Object> => {
        return this.httpClient.post(url, body);
    }

    deleteRunner = (id: number | string): Observable<Object> => {
        const params = new HttpParams().set('id', `${id}`);
        const url = `${environment.serverUrl}/api/deleterunner`;
        return this.httpClient.delete(url, { params: params });
    }

}
