import { Component, OnInit } from "@angular/core";
import { NewsService } from './news.service';
import { environment } from '../../../../environments/environment';
import { News } from 'src/app/main/pages/news/model/news.model';
import { PermissionManagerService } from 'src/app/core/permission-manager.service';
import { NgxSpinnerService } from "ngx-spinner";

@Component({
    selector: 'app-news',
    templateUrl: './news.component.html',
    styleUrls: ['./news.component.scss'],
    providers: [NewsService]
})
export class AppNewsComponent implements OnInit {


    urlPersist = `${environment.serverUrl}/api/addnews`;
    urlGetData = `${environment.serverUrl}/api/news`;
    news: News[];

    constructor(private ngxSpinnerService: NgxSpinnerService,
        private newsService: NewsService,
        private permissionManagerService: PermissionManagerService) { }

    ngOnInit(): void {
        this.refreshData();
    }

    persistNews = (formData: FormData): void => {
        this.newsService.persistNews(this.urlPersist, formData).subscribe(value => {
            this.refreshData();
        });
    }

    refreshData = (): void => {
        this.ngxSpinnerService.show();
        this.newsService.getNews(this.urlGetData).subscribe(data => {
            this.news = data;
            this.ngxSpinnerService.hide();
        });
    }

    removeNews = (id: number): void => {
        this.newsService.removeNews(id).subscribe(data => {
            this.refreshData();
        });
    }

    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }
}
