import { Component, Input } from '@angular/core';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { Runner } from '../model/runner.model';
import { PermissionManagerService } from 'src/app/core/permission-manager.service';
@Component({
    selector: 'app-grid-runner',
    templateUrl: './grid-runner.component.html',
    styleUrls: ['./grid-runner.component.scss']
})
export class AppGridRunnerComponent {
    logo = '../../../assets/image/logoEnginner.png';
    public config: PerfectScrollbarConfigInterface;

    _runners: Runner[] = [];

    @Input('runners')
    public set runners(runners: Runner[]) {
        this._runners = runners;
    }

    public get runners() {
        return this._runners;
    }

    trackByFn = (index: any, item: any): void => {
        return index;
    }


}
