import { Component, OnInit } from "@angular/core";
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { LoginService } from 'src/app/core/login.service';
import { LoginModel } from './login.model';
import { AuthenticationStatus } from './authentication-status.enum';
import { PermissionManagerService } from '../../core/permission-manager.service';

@Component({
    selector: 'app-log-dialog-cnt',
    templateUrl: './login-dialog-content.component.html',
    styleUrls: ['./login-dialog-content.component.scss']
})
export class AppLoginDialogContentComponent {
    loginModel: LoginModel = {
        username: '',
        password: ''
    };

    AuthenticationStatus: typeof AuthenticationStatus = AuthenticationStatus;

    currentAuthStatus: AuthenticationStatus = AuthenticationStatus.New;

    constructor(public activeModal: NgbActiveModal,
        private loginService: LoginService,
        private permissionManagerService: PermissionManagerService) {
    }

    authenticate = () => {
        this.loginService.runAuthentication(this.loginModel).subscribe(res => {
            localStorage.setItem('token', res.headers.get('Authorization'));
            if (Array.isArray(res.body)) {
                const role: string = res.body[0];
                localStorage.setItem('role', role);
            }
            this.currentAuthStatus = AuthenticationStatus.OK;
            this.permissionManagerService.isAuthenticated = localStorage.getItem('token').length > 0;
            this.permissionManagerService.role = localStorage.getItem('role');
            this.close();
            window.location.reload();
        }, (errpr) => {
            this.currentAuthStatus = AuthenticationStatus.Error;
            this.permissionManagerService.isAuthenticated = false;
        });
    }

    close = (): void => {
        this.activeModal.close();
    }

    properAuthentication = (): boolean => {
        const result: boolean = this.currentAuthStatus === AuthenticationStatus.OK || this.currentAuthStatus === AuthenticationStatus.New;
        return result;
    }

}
