import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';
import { NewsService } from '../../main/pages/news/news.service';
import { environment } from 'src/environments/environment';
import { PermissionManagerService } from 'src/app/core/permission-manager.service';



@Component({
    selector: 'app-image-galery',
    templateUrl: './image-galery.component.html',
    styleUrls: ['./image-galery.component.scss']
    , providers: [NewsService]
})
export class ImageGaleryComponent implements OnInit {
    galleryOptions: NgxGalleryOptions[];
    urlGetData = `${environment.serverUrl}/api/news`;
    picture: any;
    _galleryImages: NgxGalleryImage[];
    @Output() notifyReqCompleted: EventEmitter<string>;

    constructor(private newsService: NewsService, private permissionManagerService: PermissionManagerService) {
        this.notifyReqCompleted = new EventEmitter<string>();
    }

    @Input('galleryImages')
    public set galleryImages(galleryImages: NgxGalleryImage[]) {
        this._galleryImages = galleryImages;
    }

    public get galleryImages() {
        return this._galleryImages;
    }

    deleteImage(event, index): void {
        const image = this._galleryImages[index];
        this.notifyReqCompleted.emit(image.label);
    }

    ngOnInit(): void {

        this.galleryOptions = [
            {
                width: '800px',
                height: '600px',
                thumbnailsColumns: 4,
                imageAnimation: NgxGalleryAnimation.Slide,
                previewArrows: true,
                preview: true
            }
        ];

        if (this.isAuthenticated()) {
            this.galleryOptions[0].thumbnailActions =
                [{ icon: 'fa fa-times-circle', onClick: this.deleteImage.bind(this), titleText: 'delete' }];
        }
    }


    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }
}



