import { Component, Input } from "@angular/core";
import { Runner } from 'src/app/main/pages/runners/model/runner.model';

@Component({
    selector: 'app-statistic-modal-contnet',
    templateUrl: './modal-statistic-content.component.html',
    styleUrls: ['./modal-statistic-content.component.scss']
})
export class AppStatisticModalContentComponent {

    _runner: Runner = new Runner();

    @Input('runner')
    public set runner(runner: Runner) {
        this._runner = runner;
    }

    public get runner() {
        return this._runner;
    }

}
