import { Component, OnInit, OnChanges } from "@angular/core";
import {
    ChangeDetectionStrategy,
    ViewChild,
    TemplateRef
} from '@angular/core';
import {
    startOfDay,
    endOfDay,
    subDays,
    addDays,
    endOfMonth,
    isSameDay,
    isSameMonth,
    addHours
} from 'date-fns';
import { CustomDateFormatter } from './custom-date-formatter.provider';
import { Subject } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {
    CalendarEvent,
    CalendarEventAction,
    CalendarEventTimesChangedEvent,
    CalendarView
} from 'angular-calendar';
import { CalendarEventService } from './calendar-event.service';
import { PermissionManagerService } from 'src/app/core/permission-manager.service';


@Component({
    selector: 'app-calendar-event',
    templateUrl: './calendar-event.componet.html',
    styleUrls: ['./calendar-event.componet.scss'],
    providers: [CalendarEventService],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AppCalendarEventComponent implements OnInit {
    @ViewChild('modalContent') modalContent: TemplateRef<any>;

    view: CalendarView = CalendarView.Month;

    CalendarView = CalendarView;

    _viewDate: Date = new Date();

    modalData: {
        action: string;
        event: CalendarEvent;
    };

    public set viewDate(viewDate: Date) {
        this._viewDate = viewDate;
    }

    public get viewDate() {
        return this._viewDate;
    }

    locale: string = 'pl';

    refresh: Subject<any> = new Subject();

    events: CalendarEvent[];

    activeDayIsOpen: boolean = false;

    constructor(private modal: NgbModal, private caledarEventService: CalendarEventService<CalendarEvent>,
        private permissionManagerService: PermissionManagerService) { }


    ngOnInit(): void {
        this.refreshCalendarEventData();
    }

    refreshCalendarEventData = (): void => {
        this.caledarEventService.getData().subscribe(data => {
            this.convertJsonEventsData(data);
            this.events = data;
            this.events = this.events.filter(event => event.id !== null);
            this.refresh.next();
        });
    }

    convertJsonEventsData = (calendarEvents: CalendarEvent[]): void => {
        calendarEvents.forEach((calendarEvent) => {
            calendarEvent.start = calendarEvent.start ? new Date(calendarEvent.start) : calendarEvent.start;
            calendarEvent.end = calendarEvent.end ? new Date(calendarEvent.end) : calendarEvent.end;
        });
    }

    dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
        if (isSameMonth(date, this.viewDate)) {
            if (
                (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
                events.length === 0
            ) {
                this.activeDayIsOpen = false;
            } else {
                this.activeDayIsOpen = true;
            }
            this.viewDate = date;
        }
    }

    eventTimesChanged({
        event,
        newStart,
        newEnd
    }: CalendarEventTimesChangedEvent): void {
        this.events = this.events.map(iEvent => {
            if (iEvent === event) {
                return {
                    ...event,
                    start: newStart,
                    end: newEnd
                };
            }
            return iEvent;
        });
        this.handleEvent('Dropped or resized', event);
    }

    handleEvent(action: string, event: CalendarEvent): void {
    }

    addEvent(): void {
        this.events = [
            ...this.events,
            {
                title: 'New event',
                start: startOfDay(new Date()),
                end: endOfDay(new Date()),
            }
        ];
    }

    deleteEvent(eventToDelete: CalendarEvent) {
        // this.events = this.events.filter(event => event !== eventToDelete);
        this.caledarEventService.deleteCalendarEvent(eventToDelete.id).subscribe(
            result => {
                this.refreshCalendarEventData();
            }, error => {
                this.refreshCalendarEventData();
            }
        )
    }

    setView(view: CalendarView) {
        this.view = view;
    }

    closeOpenMonthViewDay() {
        this.activeDayIsOpen = false;
    }

    saveEvent(calendarEvent: CalendarEvent): void {
        this.caledarEventService.persistEventCalendar(calendarEvent).subscribe(result => {
            this.refreshCalendarEventData();
        }, error => {
            this.refreshCalendarEventData();
        });
    }

    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }
}
