import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { RequestDataModel } from './request-data.model';

@Injectable({
  providedIn: 'root'
})
export class ServerDataService<T> {
  constructor(private http: HttpClient, ) { }

  getData = (requestData: RequestDataModel): Observable<T> => {
    return this.http.get<T>(requestData.url, { headers: requestData.headers, params: requestData.httpParams });
  }

  getFileResponse = (requestData: RequestDataModel): Observable<HttpResponse<string>> => {
    return this.http.get(requestData.url, { responseType: 'text', observe: 'response' });
  }

  getForBlob = (requestData: RequestDataModel): Observable<HttpResponse<Blob>> => {
    return this.http.get<Blob>(requestData.url, { responseType: 'blob' as 'json', observe: 'response' });
  }

  postData = (requestData: RequestDataModel): Observable<Object> => {
    return this.http.post(requestData.url, requestData.body);
  }

  putData = (requestData: RequestDataModel): Observable<Object> => {
    return this.http.put(requestData.url, requestData.body, { params: requestData.httpParams });
  }

  patchData = (requestData: RequestDataModel): Observable<Object> => {
    return this.http.patch(requestData.url, requestData.body);
  }

  deleteData = (requestData: RequestDataModel): Observable<Object> => {
    return this.http.delete(requestData.url);
  }
}

export const intervalTime: number = 10000;
