import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';
import { News } from './model/news.model';
import { RequestDataModel } from 'src/app/core/request-data.model';
import { environment } from 'src/environments/environment';

@Injectable()
export class NewsService {
    constructor(private httpClient: HttpClient) { }

    persistNews = (url: string, body: FormData): Observable<Object> => {
        return this.httpClient.post(url, body);
    }

    getNews = (url: string): Observable<News[]> => {
        return this.httpClient.get<News[]>(url);
    }

    removeNews = (id: number | string): Observable<Object> => {
        const params = new HttpParams().set('id', `${id}`);
        const url = `${environment.serverUrl}/api/deletenews`;
        return this.httpClient.delete(url, { params: params });
    }

}
