import { Component, Input } from "@angular/core";
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AppStatisticModalContentComponent } from './modal-statistic-content.component';
import { Runner } from '../../main/pages/runners/model/runner.model';

@Component({
    selector: 'app-modal-statistic',
    templateUrl: './modal-statistic.component.html',
    styleUrls: ['./modal-statistic.component.scss']
})
export class AppModalStatisticComponent {

    _runner: Runner;

    @Input('runner')
    public set runner(runner: Runner) {
        this._runner = runner;
    }

    public get runner() {
        return this._runner;
    }

    constructor(public modalService: NgbModal) {
    }

    open = () => {
        const modalRef = this.modalService.open(AppStatisticModalContentComponent);
        modalRef.componentInstance.runner = this.runner;
    }

}
