import { Component, OnInit } from "@angular/core";
import { GalleryService } from './gallery.service';
import { environment } from 'src/environments/environment';
import { NgxGalleryImage } from 'ngx-gallery';
import { GridExchangeDataService } from '../runners/grid-exchange-data.service';
import { PermissionManagerService } from 'src/app/core/permission-manager.service';

@Component({
    selector: 'app-gallery-view',
    templateUrl: './gallery-view.component.html',
    styleUrls: ['./gallery-view.component.scss'],
    providers: [GalleryService, GridExchangeDataService]
})
export class GalleryViewComponent implements OnInit {

    baseUrl = `${environment.serverUrl}/api`;
    persitUrl = `${environment.serverUrl}/api/addimggalery`;
    galleryData: NgxGalleryImage[] = [];

    constructor(private galleryService: GalleryService,
        private permissionManagerService: PermissionManagerService) {

    }

    ngOnInit(): void {
        this.refreshData();
    }
    refreshData = (): void => {
        const url = `${this.baseUrl}/gallery`;
        this.galleryService.getGallery(url)
            .subscribe(data => {
                this.galleryData = data;
            });
    }

    onImageUploaded = (data: any): void => {
        this.refreshData();
    }

    deleteImage = (id: string): void => {
        this.galleryService.removeImage(id).subscribe(result => {
            this.refreshData();
        });
    }

    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }
}
