import { Component, Output, EventEmitter } from "@angular/core";
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { News } from 'src/app/main/pages/news/model/news.model';
import { FileChangeService } from 'src/app/core/file-change.service';

@Component({
    selector: 'app-modal-news-content',
    templateUrl: './modal-news-content.component.html',
    styleUrls: ['./modal-news-content.component.scss']
})
export class AppModalNewsContnetComponent {

    @Output() notifyCollectedItem: EventEmitter<FormData>;

    formData: FormData;

    news: News = {
        subject: null,
        contents: null,
        creatorName: null
    };


    constructor(public activeModal: NgbActiveModal, private fileChangeService: FileChangeService) {
        this.notifyCollectedItem = new EventEmitter<FormData>();
    }

    takeMyBreath = (): void => {
        if (!this.formData) {
            this.formData = new FormData();
            const content = '<a></a>';
            const blob = new Blob([content], { type: "text/xml" });
            this.formData.append('file', blob);
            this.appendNewsModel();
        }
        this.notifyCollectedItem.emit(this.formData);
        this.activeModal.close();
    }


    fileChange = (event: any): void => {
        if (event) {
            this.formData = this.fileChangeService.prepareFile(event);
        }

        this.appendNewsModel();
    }

    appendNewsModel = (): void => {
        this.formData.append('news', new Blob([JSON.stringify(this.news)],
            {
                type: 'application/json'
            }));
    }





}
