import { Component, Input } from "@angular/core";
import { News } from 'src/app/main/pages/news/model/news.model';
import { PermissionManagerService } from 'src/app/core/permission-manager.service';

@Component({
    selector: 'app-post',
    templateUrl: './post-block.component.html',
    styleUrls: ['./post-block.component.scss']

})
export class AppPostComponent {
    path = '../../../assets/image/race.png';
    _news: News;

    @Input('news')
    public set news(news: News) {
        this._news = news;
    }

    public get news() {
        return this._news;
    }

    constructor(private permissionManagerService: PermissionManagerService) { }

    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }

}
