import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { AppNewsComponent } from './pages/news/news.component';
import { AppRunnersComponent } from './pages/runners/runners.component';
import { AppCalendarComponent } from './pages/calendar/calendar.component';
import { AppAboutUsComponent } from './pages/about-us/about-us.component';
import { AppContactComponent } from './pages/contact/contact.component';
import { GalleryViewComponent } from './pages/gallery/gallery-view.component';
import { AppCooperationViewComponent } from './pages/cooperation/cooperation.component';


const routes: Routes = [
    { path: '', redirectTo: 'news', pathMatch: 'full' },
    {
        path: 'news',
        component: AppNewsComponent,
    },
    {
        path: 'runners',
        component: AppRunnersComponent,
    },
    {
        path: 'calendar',
        component: AppCalendarComponent
    },
    {
        path: 'about-us',
        component: AppAboutUsComponent
    },
    {
        path: 'contact',
        component: AppContactComponent
    },
    {
        path: 'gallery',
        component: GalleryViewComponent
    },
    {
        path: 'synergy',
        component: AppCooperationViewComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MainRoutingModule {
}

