import { Component } from "@angular/core";
import { PermissionManagerService } from '../../core/permission-manager.service';

@Component({
    selector: 'app-login-switch',
    templateUrl: './login-switch.component.html',
    styleUrls: ['./login-switch.component.scss']
})
export class LoginSwitchComponent {

    constructor(private permissionManagerService: PermissionManagerService) { }

    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }

    logoff = (): void => {
        this.permissionManagerService.takeOffPermission();
    }
}
