import { Component, OnInit } from "@angular/core";
import { RunnerService } from './runner.service';
import { environment } from 'src/environments/environment';
import { Runner } from './model/runner.model';
import { GridExchangeDataService } from './grid-exchange-data.service';
import { PermissionManagerService } from '../../../core/permission-manager.service';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-runners',
    templateUrl: './runners.component.html',
    styleUrls: ['./runners.component.scss'],
    providers: [RunnerService, GridExchangeDataService]
})
export class AppRunnersComponent implements OnInit {

    baseUrl = `${environment.serverUrl}/api`;

    constructor(private runnerService: RunnerService,
        private gridExchangeDataService: GridExchangeDataService,
        private permissionManagerService: PermissionManagerService,
        private ngxSpinnerService: NgxSpinnerService) { }

    runners: Runner[] = [];

    ngOnInit(): void {
        this.refreshData();

        this.gridExchangeDataService.dataSource.subscribe(date => {
            this.refreshData();
        });
    }


    refreshData = () => {
        this.ngxSpinnerService.show();
        const url = `${this.baseUrl}/runners`;
        this.runnerService.getRunners(url).subscribe(runnersData => {
            this.runners = runnersData;
            this.ngxSpinnerService.hide();
        });
    }

    getRunnerDataFromModal = (runner: Runner): void => {
        const url = `${this.baseUrl}/runner`;
        this.runnerService.persistRunner(url, runner).subscribe(runnerData => {
            this.refreshData();
        });
    }

    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }
}

