import { Injectable } from '@angular/core';
import { CalendarEvent } from 'calendar-utils';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable()
export class CalendarEventService<T> {

    private baseUrl = `${environment.serverUrl}/api`;

    constructor(private http: HttpClient) {
    }

    persistEventCalendar = (calendarEvent: T): Observable<T> => {
        const url = `${this.baseUrl}/addevent`;
        return this.http.post<T>(url, calendarEvent);
    }

    getData = (): Observable<T[]> => {
        const url = `${this.baseUrl}/getevents`;
        return this.http.get<T[]>(url);
    }

    deleteCalendarEvent = (id: number | string): Observable<Object> => {
        // const httpParams: HttpParams = new HttpParams();
        // httpParams.set('id', `${id}`);
        // const httpOptions = {
        //     params: httpParams
        // };
        const params = new HttpParams().set('id', `${id}`);
        const url = `${this.baseUrl}/deleteevent`;
        return this.http.delete(url, { params: params });
    }
}
