import { Component, Input } from "@angular/core";
import { environment } from 'src/environments/environment';
import { Runner } from '../model/runner.model';
import { RunnerService } from '../runner.service';
import { GridExchangeDataService } from '../grid-exchange-data.service';
import { PermissionManagerService } from 'src/app/core/permission-manager.service';

@Component({
    selector: 'app-grid-row',
    templateUrl: './grid-runner-row.component.html',
    styleUrls: ['./grid-runner-row.component.scss']
})
export class AppGridRunnerRowComponent {

    path = '../../../assets/image/running.png';
    _runner: Runner;
    uploadApiUrl: string;
    baseUrl = environment.serverUrl;

    constructor(private runerService: RunnerService,
        private gridExchangeDataService: GridExchangeDataService,
        private permissionManagerService: PermissionManagerService) { }

    @Input('runner')
    public set runner(runner: Runner) {
        this._runner = runner;
        this.uploadApiUrl = `${environment.serverUrl}/api/${this._runner.id}/runner`;
    }

    public get runner() {
        return this._runner;
    }

    onPhotoSelected = (formData: FormData): void => {
        this.runerService.uploadRunnerPhoto(this.uploadApiUrl, formData).subscribe(data => {
            this.gridExchangeDataService.notifyDataChange('completed');
        });
    }

    removeRunner = (): void => {
        this.runerService.deleteRunner(this.runner.id).subscribe(data => {
            this.gridExchangeDataService.notifyDataChange('completed');
        });

    }

    isAuthenticated = (): boolean => {
        return this.permissionManagerService.hasFullPermission();
    }
}
