import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from "@angular/core";
import { SharedModule } from '../shared/shared.module';
import { MainComponent } from './main.component';
import { RouterModule } from '@angular/router';
import { MainRoutingModule } from './main.routing';
import { AppNewsComponent } from './pages/news/news.component';
import { AppGridRunnerComponent } from './pages/runners/grid-runner/grid-runner.component';
import { AppGridRunnerRowComponent } from './pages/runners/grid-runner/grid-runner-row.component';
import { AppRunnersComponent } from './pages/runners/runners.component';
import {
    PerfectScrollbarModule,
    PerfectScrollbarConfigInterface,
    PERFECT_SCROLLBAR_CONFIG
} from 'ngx-perfect-scrollbar';
import { AppRunnerHeaderComponent } from './pages/runners/runner-header/runner-header.component';
import { AppContactComponent } from './pages/contact/contact.component';
import { AppAboutUsComponent } from './pages/about-us/about-us.component';
import { GalleryViewComponent } from './pages/gallery/gallery-view.component';
import { AppCooperationViewComponent } from './pages/cooperation/cooperation.component';
import { NgxSpinnerService, NgxSpinnerModule } from 'ngx-spinner';

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
    suppressScrollX: false
};


@NgModule({
    imports: [SharedModule, RouterModule, MainRoutingModule, PerfectScrollbarModule, NgxSpinnerModule],
    declarations: [MainComponent,
        AppNewsComponent,
        AppGridRunnerComponent,
        AppGridRunnerRowComponent,
        AppRunnersComponent,
        AppRunnerHeaderComponent,
        AppContactComponent,
        AppAboutUsComponent,
        GalleryViewComponent,
        AppCooperationViewComponent],
    exports: [MainComponent,
        AppNewsComponent,
        AppGridRunnerComponent,
        AppGridRunnerRowComponent,
        AppRunnersComponent,
        AppContactComponent,
        AppAboutUsComponent],
    providers: [{
        provide: PERFECT_SCROLLBAR_CONFIG,
        useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    }],
    schemas: [CUSTOM_ELEMENTS_SCHEMA],
})
export class MainModule { }
