import { Component, Output, EventEmitter, Input } from "@angular/core";
import { FileChangeService } from 'src/app/core/file-change.service';
import { HttpClient } from '@angular/common/http';
import { GridExchangeDataService } from 'src/app/main/pages/runners/grid-exchange-data.service';

@Component({
    selector: 'app-file-uploader',
    templateUrl: './file-uploader.component.html',
    styleUrls: ['./file-uploader.component.scss']
})
export class AppFileUploaderComponent {
    _url: string;
    @Output() notifyReqCompleted: EventEmitter<string>;
    model: any = {};

    constructor(private http: HttpClient, private fileChangeService: FileChangeService,
        private gridExchangeDataService: GridExchangeDataService) {
        this.notifyReqCompleted = new EventEmitter<string>();
    }
    @Input('url')
    public set url(url: string) {
        this._url = url;
    }

    public get url() {
        return this._url;
    }

    fileChange = (event: any): void => {
        const body: FormData = this.fileChangeService.prepareFile(event);
        this.http.post(this.url, body).
            subscribe(
                (data) => {
                    this.notifyReqCompleted.emit('completed');
                    this.gridExchangeDataService.notifyDataChange('completed');
                }
            );
    }
}
